@import '~antd/dist/antd.css';

*{

    padding: 0;

    margin: 0;

}

ul,li{

    list-style: none;

}

body{

    background-color: #ddd;

}

/* header */

.headerwrap{

    height: 50px;

    background-color: #444;

}

.headerwrap header{

    width: 1200px;

    margin: 0 auto;

}

.headerwrap img{

    margin-top: 10px;

    width: 120px;

}

.headerwrap a{

    margin: 0 20px;

    color: #fff;

}

/* main */

.main{

    width: 1200px;

    margin: 30px auto;

}

.content{

    width: 900px;

    float: left;

    background-color: white;

    border-radius: 4px;

}

.homeheader{
    width: 900px;

    height: 40px;

    background-color: #f6f6f6;

    border-radius: 4px;
}

.homeheaderlink{
    color: #80bd01;
    font-size: 14px;
    padding-left: 28px;
    line-height: 40px;
    float: left;
}

.sider{

    width: 270px;

    height: 500px;

    float: right;

    border-radius: 6px;

}

.sider1{
    background-color: white;
    width: 270px;
    height: 240px;
    border-radius: 6px;
    margin-bottom: 20px;
}

.sider1 p{
    font-size: 15px;
    line-height: 35px;
    padding-left: 15px;
}

.sider2{
    background-color: white;
    width: 270px;
    height: 240px;
    border-radius: 6px;
}

.form1{
    margin-top:50px;
    margin-left: 140px;
}

.anniu{
    width: 140px;
}

.code1{
    color: black;
    font-family: 'YouYuan';
    padding-top:4px;
    padding-right: 6px;
    padding-bottom: 4px;
    padding-left: 6px;
    background-color: #f7f7f7;
}

.code2{
    color: black;
    font-family: 'YouYuan';
    width:850px;
    background-color: #f7f7f7;
}

.touxiang{
    height: 24px;
    float: left;
    margin-right: 6px;
    margin-left:15px;
    border-radius: 3px;
}

.reply_count{
    font-size: 16px;
    color: black;
    float: left;
}

.visit_count{
    font-size: 16px;
    float: left;
}

.tab{
    font-size: 16px;
    float: left;
}

.title{
    width:610px;
    height: 24px;
    font-size: 16px;
    color: black;
    overflow: hidden;
    float: left;
    position: absolute;
    left: 230px;
}

.touxiang2{
    height: 20px;
    float: left;
    padding-left: 65px;
}

.time{
    font-size: 16px;
    color: black;
    float: left;
    width: 70px;
}

#li{
    padding-top: 3px;
    padding-left: 12px;
    border-left: 1px;
    border-right: 0px;
    border-style: solid;
    border-color: #ddd;
    /* border: 0 solid #ddd; */
    float: left;
    color: #999;
    cursor: default;
    background-color: transparent;
    height: 32px;
    width: 30px;
}

